export async function getAllNFTs(owner:string) {
  let accountList: MetadataData[] = [];

  let foundAccounts = await Metadata.findDataByOwner(connection, owner);
  accountList.push(...foundAccounts);

  return accountList;
}

export async function getAllStakedNFTs(owner: string, pool?: string) {
  let accountList: MetadataData[] = [];

  let f = [];

  if (pool) {
    f.push({
      memcmp: {
        offset: 8,
        bytes: pool,
      },
    });
  }

  f.push({
    memcmp: {
      offset: 8 + 32,
      bytes: owner,
    },
  });

  let farmers = await farmProgram.account.farmer.all(f);

  if (!farmers || farmers.length == 0) {
    return accountList;
  }

  let c = 1;
  for (let i = 0; i < farmers.length; i++) {

    // console.log(c, "poolName:", poolNames[farmers[i].account.farm.toBase58()]);
    // console.log(c, "identity:", farmers[i].account.identity.toBase58());
    // console.log(c, "farm (pool id):", farmers[i].account.farm.toBase58());
    // console.log(c, "gemsStakedCount:", farmers[i].account.gemsStaked.toNumber());

    const [authority_pubkey, authority_bump] =
      await anchor.web3.PublicKey.findProgramAddress(
        [farmers[i].account.vault.toBuffer()],
        bankProgram.programId
      );

    let foundAccounts = await Metadata.findDataByOwner(
      connection,
      authority_pubkey.toBase58()
    );

    accountList.push(...foundAccounts);
  }

  return accountList;
}
